import { Card, Typography, Box } from '@mui/material';
import React from 'react';

interface Props {
  title: string;
  description?: string;
  image: string;
  onClick?: () => void;
}
const BenefitCard = ({ title, description, image, onClick }: Props) => {
  return (
    <Card
      sx={{
        border: 'none',
        padding: 2,
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        cursor: 'pointer',
        transition: 'transform .2s ease-in',
        '&:hover': {
          transform: 'translateY(-10px)',
        },
      }}
      onClick={onClick}
    >
      <Box component="img" src={image} alt={title} sx={{ width: 140, marginBottom: 2 }} />
      <Typography
        variant="h6"
        sx={{
          marginBottom: '16px',
          fontSize: { md: 20, xs: 18 },
          textAlign: 'center',
          whiteSpace: 'pre-line',
        }}
      >
        {title}
      </Typography>
      {description ? <Typography sx={{ whiteSpace: 'pre-line', fontSize: 14 }}>{description}</Typography> : null}
    </Card>
  );
};

export default BenefitCard;
