import React, { useRef } from 'react';
import { useSpring, animated } from 'react-spring';
import AnimatedAngle from 'src/components/Animated/AnimatedAngle';
import { Box } from '@mui/material';
import { useScrollDown } from 'src/hooks/useScrollDown';

const MainJumbotron = () => {
  const containerRef = useRef<HTMLDivElement>(null);

  const titleAnimation = useSpring({
    from: { opacity: 0, transform: 'translateX(-30px)' },
    to: { opacity: 1, transform: 'translateX(0)' },
  });
  const descriptionAnimation = useSpring({
    from: { opacity: 0, transform: 'translateX(30px)' },
    to: { opacity: 1, transform: 'translateX(0)' },
  });
  const bottomArrowAnimation = useSpring({
    from: { opacity: 0, transform: 'translateY(-30px)' },
    to: { opacity: 1, transform: 'translateY(0)' },
    delay: 300,
  });

  const scrollToBottom = useScrollDown(containerRef);

  return (
    <Box ref={containerRef} sx={{ position: 'relative', height: '100vh' }}>
      <Box
        sx={{
          position: 'absolute',
          top: {
            md: '45%',
            xs: '30%',
          },
          left: '50%',
          transform: 'translate(-50%, -50%)',
        }}
      >
        <Box
          component={animated.h1}
          style={titleAnimation}
          sx={{
            fontStyle: 'italic',
            fontSize: {
              md: '3.5rem',
              xs: '2rem',
            },
            textAlign: 'center',
            whiteSpace: 'nowrap',
          }}
        >
          Growth
          <Box component="span" sx={{ color: 'secondary.main' }}>
            .
          </Box>{' '}
          And Share
        </Box>
        <Box
          component={animated.h2}
          style={descriptionAnimation}
          sx={{ textAlign: 'center', fontWeight: 200, fontSize: '1.4rem', fontStyle: 'italic' }}
        >
          This is <strong>Lubycon</strong>
        </Box>
      </Box>
      <Box sx={{ position: 'absolute', bottom: '20%', left: '50%', transform: 'translateX(-50%)' }}>
        <animated.div style={bottomArrowAnimation}>
          <AnimatedAngle onClick={scrollToBottom} />
        </animated.div>
      </Box>
    </Box>
  );
};

export default MainJumbotron;
