import { Container, Grid, Typography, Theme, Box, useTheme } from '@mui/material';
import React from 'react';
import { useAllMembers } from 'src/hooks/members/useAllMembers';
import MemberCard from 'components/Cards/MemberCard';
import deepPurple from '@mui/material/colors/deepPurple';
import FadeUp from '../Animated/FadeUp';

const MemberSection = () => {
  const { spacing } = useTheme<Theme>();
  const members = useAllMembers('lubycon');

  return (
    <Box
      component="section"
      sx={{
        padding: {
          md: `${spacing(15)} 0`,
          xs: `${spacing(10)} 0`,
        },
      }}
    >
      <Container>
        <Typography
          variant="h4"
          component="h2"
          gutterBottom={true}
          sx={{
            fontWeight: 'bold',
            textAlign: 'center',
            fontSize: {
              md: 'h4.fontSize',
              xs: 'h6.fontSize',
            },
          }}
        >
          루비콘 팀의 멤버들이에요
        </Typography>
        <Typography sx={{ marginBottom: 10 }}></Typography>
        <Grid container spacing={2}>
          {members.map(mentor => (
            <Grid item key={mentor.id} xs={6} sm={3}>
              <FadeUp>
                <MemberCard member={mentor} backgroundColor={deepPurple['100']} />
              </FadeUp>
            </Grid>
          ))}
        </Grid>
      </Container>
    </Box>
  );
};

export default MemberSection;
