import { Container, Grid, Typography, Box } from '@mui/material';
import React, { useRef, useState } from 'react';
import Emoji from 'components/Utils/Emoji';
import AnimatedAngleIcon from '../Animated/AnimatedAngle';
import { useScrollDown } from 'src/hooks/useScrollDown';
import { animated, useSpring } from 'react-spring';
import { ImpressionArea } from '@lubycon/react';

const IntroSection = () => {
  const containerRef = useRef<HTMLDivElement>(null);
  const [visible, setVisible] = useState(false);

  const titleAnimation = useSpring({
    opacity: visible ? 1 : 0,
    transform: visible ? 'translateX(0)' : 'translateX(-50px)',
  });
  const descriptionAnimation = useSpring({
    opacity: visible ? 1 : 0,
    transform: visible ? 'translateX(0)' : 'translateX(-50px)',
    delay: 300,
  });

  const scrollToBottom = useScrollDown(containerRef);

  return (
    <Container component="section">
      <Grid
        ref={containerRef}
        container
        justifyContent="center"
        alignItems="center"
        sx={{ position: 'relative', height: '100vh' }}
      >
        <Grid
          item
          xs={12}
          sm
          sx={{
            paddingTop: 0,
          }}
        >
          <ImpressionArea onImpressionStart={() => setVisible(true)}>
            <animated.div style={titleAnimation}>
              <Typography
                variant="h4"
                component="h2"
                gutterBottom={true}
                sx={{
                  fontWeight: 'bold',
                  fontSize: {
                    md: 'h4.fontSize',
                    xs: 'h6.fontSize',
                  },
                }}
              >
                안녕하세요. 우리는 루비콘 입니다 <Emoji emoji="👋" name="Hi" />
              </Typography>
            </animated.div>
            <animated.div style={descriptionAnimation}>
              <Typography sx={{ marginBottom: 10 }}>
                루비콘은 국내 IT업계에 선한 영향을 만들어내고 싶은 개발자들과 디자이너들이 모인 팀이에요.
                <br />
                우리는 지속적으로 새로운 것들에 도전하며 실패하고, 그 과정 속에서 배운 것들을 공유해요. 그리고 이런 공유
                활동은 루비콘 팀 뿐만 아니라 팀 외부에서도 이루어지고 있어요.
              </Typography>
            </animated.div>
          </ImpressionArea>
        </Grid>
        <Box sx={{ position: 'absolute', bottom: '10%', left: '50%', transform: 'translateX(-50%)' }}>
          <AnimatedAngleIcon onClick={scrollToBottom} />
        </Box>
      </Grid>
    </Container>
  );
};

export default IntroSection;
